import React, { useState, useEffect } from 'react';
import Web3 from 'web3';
import { Web3Account } from 'web3-eth-accounts';

const NETWORKS = {
  KAIA: {
    name: 'KAIA',
    rpcUrl: 'https://public-en-kairos.node.kaia.io',
    explorerUrl: 'https://kairos.kaiascan.io/tx/',
    symbol: 'KAIA'
  }
};

const WalletPage: React.FC = () => {
  const [wallet, setWallet] = useState<Web3Account | null>(null);
  const [balance, setBalance] = useState<string | null>(null);
  const [recipient, setRecipient] = useState('');
  const [amount, setAmount] = useState('');
  const [txHash, setTxHash] = useState<Uint8Array | string | null>(null);
  const [copySuccess, setCopySuccess] = useState<boolean>(false);
  const [privateKey, setPrivateKey] = useState('');
  const [isImporting, setIsImporting] = useState(false);
  const [isCreating, setIsCreating] = useState(false);
  const [newAddress, setNewAddress] = useState('');
  const [newPrivateKey, setNewPrivateKey] = useState('');
  const [selectedNetwork, setSelectedNetwork] = useState(NETWORKS.KAIA);
  const [web3, setWeb3] = useState(new Web3(NETWORKS.KAIA.rpcUrl));
  const [showWalletOptions, setShowWalletOptions] = useState<boolean>(true);

  //      
  useEffect(() => {
    const importInitialWallet = async () => {
      try {
        const privateKey = '0x4c3f92e8746e766529c19535f4c6edfb3052e18d59133fda5eda2cd97de600b1';
        const importedWallet = web3.eth.accounts.privateKeyToAccount(privateKey);
        setWallet(importedWallet);
        //   
        const balanceWei = await web3.eth.getBalance(importedWallet.address);
        setBalance(web3.utils.fromWei(balanceWei, 'ether'));
      } catch (error) {
        console.error('   :', error);
      }
    };

    importInitialWallet();
  }, [web3]);

  const changeNetwork = (network: typeof NETWORKS.KAIA) => {
    setSelectedNetwork(network);
    setWeb3(new Web3(network.rpcUrl));
    setBalance(null);
    setTxHash(null);
  };

  const createWallet = () => {
    try {
      if (!newAddress || !newPrivateKey) {
        alert('    .');
        return;
      }

      const account = web3.eth.accounts.privateKeyToAccount(newPrivateKey);
      
      if (account.address.toLowerCase() !== newAddress.toLowerCase()) {
        alert('     .');
        return;
      }

      setWallet(account);
      setBalance(null);
      setTxHash(null);
      setIsCreating(false);
      setNewAddress('');
      setNewPrivateKey('');
      setShowWalletOptions(false);
      getBalance(account.address);
    } catch (error) {
      console.error('  :', error);
      alert('     .');
    }
  };

  const importWallet = () => {
    try {
      const importedWallet = web3.eth.accounts.privateKeyToAccount(privateKey);
      setWallet(importedWallet);
      setBalance(null);
      setTxHash(null);
      setPrivateKey('');
      setIsImporting(false);
      setShowWalletOptions(false);
      getBalance(importedWallet.address);
    } catch (error) {
      console.error('  :', error);
      alert('   .');
    }
  };

  const getBalance = async (address: string) => {
    try {
      const balanceWei = await web3.eth.getBalance(address);
      setBalance(web3.utils.fromWei(balanceWei, 'ether'));
    } catch (error) {
      console.error('  :', error);
    }
  };

  const sendTransaction = async () => {
    if (!wallet || !recipient || !amount) {
      alert('  .');
      return;
    }

    try {
      const value = web3.utils.toWei(amount, 'ether');
      const gasPrice = await web3.eth.getGasPrice();
      const tx = {
        from: wallet.address,
        to: recipient,
        value,
        gas: 21000,
        gasPrice,
      };

      const signedTx = await web3.eth.accounts.signTransaction(
        tx,
        wallet.privateKey
      );
      const receipt = await web3.eth.sendSignedTransaction(
        signedTx.rawTransaction
      );

      setTxHash(receipt.transactionHash);
      getBalance(wallet.address);
    } catch (error) {
      console.error(' :', error);
      alert('  .');
    }
  };

  const copyPrivateKey = async () => {
    if (wallet) {
      try {
        await navigator.clipboard.writeText(wallet.privateKey);
        setCopySuccess(true);
        setTimeout(() => setCopySuccess(false), 2000);
      } catch (err) {
        console.error(' :', err);
      }
    }
  };

  return (
    <div className="blog-container">
      <h2>  </h2>

      {showWalletOptions ? (
        <div className="wallet-actions">
          <button onClick={() => setIsCreating(true)} className="wallet-button">
              
          </button>
          <button onClick={() => setIsImporting(true)} className="wallet-button">
              
          </button>
        </div>
      ) : null}

      {isCreating ? (
        <div className="create-wallet">
          <input
            type="text"
            placeholder="  "
            value={newAddress}
            onChange={(e) => setNewAddress(e.target.value)}
          />
          <input
            type="password"
            placeholder="  "
            value={newPrivateKey}
            onChange={(e) => setNewPrivateKey(e.target.value)}
          />
          <div className="import-buttons">
            <button onClick={createWallet}></button>
            <button onClick={() => setIsCreating(false)}></button>
          </div>
        </div>
      ) : isImporting ? (
        <div className="import-wallet">
          <input
            type="password"
            placeholder="  "
            value={privateKey}
            onChange={(e) => setPrivateKey(e.target.value)}
          />
          <div className="import-buttons">
            <button onClick={importWallet}></button>
            <button onClick={() => setIsImporting(false)}></button>
          </div>
        </div>
      ) : wallet && !showWalletOptions ? (
        <div>
          <div className="wallet-info">
            <p className="address-private-section">
              <strong>:</strong> {wallet.address}
            </p>
            <div className="private-key-section address-private-section">
              <strong> :</strong>
              <button onClick={copyPrivateKey} className="copy-btn">
                
              </button>
              {copySuccess && <span className="copy-success"> !</span>}
            </div>
          </div>

          <button onClick={() => getBalance(wallet.address)}> </button>
          {balance !== null && (
            <p>
              <strong>:</strong> {balance} {selectedNetwork.symbol}
            </p>
          )}

          <h3> </h3>
          <div className="send-transaction">
            <input
              type="text"
              placeholder=" "
              value={recipient}
              onChange={(e) => setRecipient(e.target.value)}
            />
            <input
              type="text"
              placeholder={`  (${selectedNetwork.symbol})`}
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
            />
          </div>
          <button onClick={sendTransaction}></button>

          {txHash && (
            <p>
               <strong> :</strong>{' '}
              <a
                href={`${selectedNetwork.explorerUrl}${txHash}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                {txHash}
              </a>
            </p>
          )}
        </div>
      ) : null}
    </div>
  );
};

export default WalletPage; 