import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import ScrollToTop from './ScrollToTop';

const CommentPage: React.FC = () => {
  const [comments, setComments] = useState<Array<{text: string, timestamp: string}>>([]);
  const [input, setInput] = useState<string>("");
  const [searchQuery, setSearchQuery] = useState<string>("");
  const location = useLocation();

  useEffect(() => {
    const storedComments = localStorage.getItem(`comments-${location.pathname}`);
    if (storedComments) {
      try {
        const parsedComments = JSON.parse(storedComments);
        const migratedComments = Array.isArray(parsedComments) 
          ? parsedComments.map(comment => 
              typeof comment === 'string' 
                ? { 
                    text: comment, 
                    timestamp: new Date().toLocaleString('ko-KR', {
                      year: 'numeric',
                      month: '2-digit',
                      day: '2-digit',
                      hour: '2-digit',
                      minute: '2-digit',
                      hour12: false
                    })
                  }
                : comment
            )
          : [];
        setComments(migratedComments);
        if (migratedComments.length > 0) {
          localStorage.setItem(`comments-${location.pathname}`, JSON.stringify(migratedComments));
        }
      } catch (error) {
        console.error('Error parsing comments:', error);
        setComments([]);
      }
    } else {
      setComments([]);
    }
  }, [location.pathname]);

  const saveComments = (newComments: Array<{text: string, timestamp: string}>) => {
    localStorage.setItem(`comments-${location.pathname}`, JSON.stringify(newComments));
  };

  const addComment = () => {
    if (input.trim() === "") return;

    const now = new Date();
    const timestamp = now.toLocaleString('ko-KR', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      hour12: false
    });

    const newComments = [...comments, { text: input.trim(), timestamp }];
    setComments(newComments);
    saveComments(newComments);
    setInput("");
  };

  const deleteComment = (index: number) => {
    const newComments = comments.filter((_, i) => i !== index);
    setComments(newComments);
    saveComments(newComments);
  };

  const filteredComments = comments.filter(comment => 
    comment && comment.text && comment.text.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="blog-container">
      {location.pathname === '/about' ? (
        <>
          <h2 className="about-title">  </h2>
          <div className="about-image">
            <img src="https://t4.ftcdn.net/jpg/02/96/25/53/360_F_296255302_5Ukto7ViJj9SCNTyoUpfyw5b2mGzEusA.jpg" alt=" " />
          </div>
          <div className="about-description">
            <p>     ,       .<br />         .<br />           .</p>
            <p>  : <a href="mailto:josm100@naver.com">josm100@naver.com</a></p>
          </div>
        </>
      ) : (
        <>
          <h2>   {location.pathname === '/school' ? '' :
            location.pathname === '/work' ? '' :
            location.pathname === '/family' ? '' :
            location.pathname === '/friends' ? '' :
            location.pathname === '/lover' ? '' : ''} .<br />
            {location.pathname === '/school' ? '' :
            location.pathname === '/work' ? '' :
            location.pathname === '/family' ? '' :
            location.pathname === '/friends' ? '' :
            location.pathname === '/lover' ? '' : ''}    .</h2>
          <div className="search-container">
            <input
              type="text"
              placeholder=" ..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="search-input"
            />
            {searchQuery && (
              <button 
                className="clear-search"
                onClick={() => setSearchQuery("")}
                title=" "
              >
                
              </button>
            )}
          </div>
          <div className="comment-form">
            <textarea
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder=" ..."
            />
            <button onClick={addComment}> </button>
          </div>
          <div className="comments">
            {filteredComments.map((comment, index) => (
              <div key={index} className="comment">
                <div className="comment-content">
                  <span className="comment-text">{comment.text}</span>
                  <span className="comment-time">{comment.timestamp}</span>
                </div>
                <button onClick={() => deleteComment(index)}></button>
              </div>
            ))}
            {filteredComments.length === 0 && searchQuery && (
              <div className="no-results">
                  .
              </div>
            )}
          </div>
        </>
      )}
      <footer>
         2025     : 
      </footer>
      <ScrollToTop />
    </div>
  );
};

export default CommentPage; 